import { BrowserRouter, Routes, Route, Link } from 'react-router-dom'
import { NavLinkWrappers } from './components/containers'
import { useState } from 'react'
// import ZAD2 from './components/ZAD2/ZAD2'
// import ZAD3 from './components/ZAD3/ZAD3'
// import ZAD4 from './components/ZAD4/ZAD4'
import ZAD1 from './components/ZAD1'
import ZAD2 from './components/ZAD2'
import ZAD3 from './components/ZAD3'
const App = () => {
  const [link, setLink] = useState<number | null>(1)

  return (
    <BrowserRouter>
      <Routes>
        <Route path={'/'} element={<ZAD1 />} />
        <Route path={'/1'} element={<ZAD1 />} />
        <Route path={'2'} element={<ZAD2 />} />
        <Route path={'/3'} element={<ZAD3 />} />
      </Routes>
      <NavLinkWrappers>
        <Link
          to="/1"
          className={link === 1 ? 'active' : ''}
          onClick={() => setLink(1)}
        >
          Zadanie 1
        </Link>
        <Link
          to="/2"
          className={link === 2 ? 'active' : ''}
          onClick={() => setLink(2)}
        >
          Zadanie 2
        </Link>
        <Link
          to="/3"
          className={link === 3 ? 'active' : ''}
          onClick={() => setLink(3)}
        >
          Zadanie 3
        </Link>
      </NavLinkWrappers>
    </BrowserRouter>
  )
}

export default App
