import * as math from 'mathjs'
import { N, Tc, trim, za, zf, zp, widmo, trimToWidth } from '../functions'
//@ts-ignore
import linspace from 'linspace'
import { Button, Table } from '../containers'
import html2canvas from 'html2canvas'

const ZAD3 = () => {
  const id = Math.floor(Math.random() * 100) + 1

  const n: Array<number> = linspace(0, Tc, N)

  var temp: Array<number> = []
  n.map(element => temp.push(za(element, 0.5)))
  const widmo1 = widmo(temp).M

  temp = []
  n.map(element => temp.push(za(element, 7)))
  const widmo2 = widmo(temp).M

  temp = []
  n.map(element => temp.push(za(element, 50)))
  const widmo3 = widmo(temp).M

  temp = []
  n.map(element => temp.push(zp(element, 0.3)))
  const widmo4 = widmo(temp).M

  temp = []
  n.map(element => temp.push(zp(element, 2.57)))
  const widmo5 = widmo(temp).M

  temp = []
  n.map(element => temp.push(zp(element, 3 * math.pi)))
  const widmo6 = widmo(temp).M

  temp = []
  n.map(element => temp.push(zf(element, 0.5)))
  const widmo7 = widmo(temp).M

  temp = []
  n.map(element => temp.push(zf(element, math.pi / 2)))
  const widmo8 = widmo(temp).M

  temp = []
  n.map(element => temp.push(zf(element, 4 * math.pi)))
  const widmo9 = widmo(temp).M

  const trim1 = trim(widmo1, 3)
  const trim2 = trim(widmo2, 3)
  const trim3 = trim(widmo3, 3)
  const trim4 = trim(widmo4, 3)
  const trim5 = trim(widmo5, 3)
  const trim6 = trim(widmo6, 3)
  const trim7 = trim(widmo7, 3)
  const trim8 = trim(widmo8, 3)
  const trim9 = trim(widmo9, 3)
  const trim10 = trim(widmo1, 6)
  const trim11 = trim(widmo2, 6)
  const trim12 = trim(widmo3, 6)
  const trim13 = trim(widmo4, 6)
  const trim14 = trim(widmo5, 6)
  const trim15 = trim(widmo6, 6)
  const trim16 = trim(widmo7, 6)
  const trim17 = trim(widmo8, 6)
  const trim18 = trim(widmo9, 6)
  const trim19 = trim(widmo1, 12)
  const trim20 = trim(widmo2, 12)
  const trim21 = trim(widmo3, 12)
  const trim22 = trim(widmo4, 12)
  const trim23 = trim(widmo5, 12)
  const trim24 = trim(widmo6, 12)
  const trim25 = trim(widmo7, 12)
  const trim26 = trim(widmo8, 12)
  const trim27 = trim(widmo9, 12)

  const width1 = trimToWidth(trim1)
  const width2 = trimToWidth(trim2)
  const width3 = trimToWidth(trim3)
  const width4 = trimToWidth(trim4)
  const width5 = trimToWidth(trim5)
  const width6 = trimToWidth(trim6)
  const width7 = trimToWidth(trim7)
  const width8 = trimToWidth(trim8)
  const width9 = trimToWidth(trim9)
  const width10 = trimToWidth(trim10)
  const width11 = trimToWidth(trim11)
  const width12 = trimToWidth(trim12)
  const width13 = trimToWidth(trim13)
  const width14 = trimToWidth(trim14)
  const width15 = trimToWidth(trim15)
  const width16 = trimToWidth(trim16)
  const width17 = trimToWidth(trim17)
  const width18 = trimToWidth(trim18)
  const width19 = trimToWidth(trim19)
  const width20 = trimToWidth(trim20)
  const width21 = trimToWidth(trim21)
  const width22 = trimToWidth(trim22)
  const width23 = trimToWidth(trim23)
  const width24 = trimToWidth(trim24)
  const width25 = trimToWidth(trim25)
  const width26 = trimToWidth(trim26)
  const width27 = trimToWidth(trim27)

  return (
    <div
      style={{
        margin: '0 auto',
        textAlign: 'center',
      }}
    >
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        <Table id={id.toString()}>
          <thead>
            <tr>
              <th colSpan={4}>Szerokość pasma</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td rowSpan={4}>Modyfikacja amplitudy</td>
              <td colSpan={3}>
                D<sub>3dB</sub>
              </td>
            </tr>
            <tr>
              <td>{width1}</td>
              <td>{width2}</td>
              <td>{width3}</td>
            </tr>
            <tr>
              <td>{width4}</td>
              <td>{width5}</td>
              <td>{width6}</td>
            </tr>
            <tr>
              <td>{width7}</td>
              <td>{width8}</td>
              <td>{width9}</td>
            </tr>
            <tr>
              <td rowSpan={4}>Modyfikacja fazy</td>
              <td colSpan={3}>
                D<sub>6dB</sub>
              </td>
            </tr>
            <tr>
              <td>{width10}</td>
              <td>{width11}</td>
              <td>{width12}</td>
            </tr>
            <tr>
              <td>{width13}</td>
              <td>{width14}</td>
              <td>{width15}</td>
            </tr>
            <tr>
              <td>{width16}</td>
              <td>{width17}</td>
              <td>{width18}</td>
            </tr>
            <tr>
              <td rowSpan={4}>Modyfikacja Amplitudy</td>
              <td colSpan={3}>
                D<sub>12dB</sub>
              </td>
            </tr>
            <tr>
              <td>{width19}</td>
              <td>{width20}</td>
              <td>{width21}</td>
            </tr>
            <tr>
              <td>{width22}</td>
              <td>{width23}</td>
              <td>{width24}</td>
            </tr>
            <tr>
              <td>{width25}</td>
              <td>{width26}</td>
              <td>{width27}</td>
            </tr>
          </tbody>
        </Table>
      </div>
    </div>
  )
}

export default ZAD3
