import { za, zp, zf, N, Tc } from '../functions'
//@ts-ignore
import linspace from 'linspace'
import Graph from '../graph/Graph'
import * as math from 'mathjs'
import { Button, NavigationBar, UL } from '../containers'
import { useState } from 'react'
const ZAD1 = () => {
  const n: Array<number> = linspace(0, Tc, N)
  const graphData1: Array<object> = []
  const graphData2: Array<object> = []
  const graphData3: Array<object> = []
  const graphData4: Array<object> = []
  const graphData5: Array<object> = []
  const graphData6: Array<object> = []
  const graphData7: Array<object> = []
  const graphData8: Array<object> = []
  const graphData9: Array<object> = []

  n.map(number => graphData1.push({ x: number, y: za(number, 0.5) }))
  n.map(number => graphData2.push({ x: number, y: za(number, 7) }))
  n.map(number => graphData3.push({ x: number, y: za(number, 50) }))
  n.map(number => graphData4.push({ x: number, y: zp(number, 0.3) }))
  n.map(number => graphData5.push({ x: number, y: zp(number, 2.57) }))
  n.map(number => graphData6.push({ x: number, y: zp(number, 3 * math.pi) }))
  n.map(number => graphData7.push({ x: number, y: zf(number, 0.5) }))
  n.map(number => graphData8.push({ x: number, y: zf(number, math.pi / 2) }))
  n.map(number => graphData9.push({ x: number, y: zf(number, 4 * math.pi) }))

  const [graph, setGraph] = useState<number | null>(1)
  return (
    <>
      <NavigationBar>
        <UL>
          <p
            style={{
              fontSize: '1rem',
              fontStyle: 'italic',
              marginTop: 0,
            }}
          >
            Modyfikacja amplitudy
          </p>
          <li>
            <Button
              onClick={() => setGraph(1)}
              className={graph === 1 ? 'activeclass' : ''}
            >
              Wykres I
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(2)}
              className={graph === 2 ? 'activeclass' : ''}
            >
              Wykres II
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(3)}
              className={graph === 3 ? 'activeclass' : ''}
            >
              Wykres III
            </Button>
          </li>
          <p
            style={{
              fontSize: '1rem',
              fontStyle: 'italic',
              marginTop: 0,
            }}
          >
            Modyfikacja fazy
          </p>
          <li>
            <Button
              onClick={() => setGraph(4)}
              className={graph === 4 ? 'activeclass' : ''}
            >
              Wykres IV
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(5)}
              className={graph === 5 ? 'activeclass' : ''}
            >
              Wykres V
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(6)}
              className={graph === 6 ? 'activeclass' : ''}
            >
              Wykres VI
            </Button>
          </li>
          <p
            style={{
              fontSize: '1rem',
              fontStyle: 'italic',
              marginTop: 0,
            }}
          >
            Modyfikacja częstotliwości
          </p>
          <li>
            <Button
              onClick={() => setGraph(7)}
              className={graph === 7 ? 'activeclass' : ''}
            >
              Wykres VII
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(8)}
              className={graph === 8 ? 'activeclass' : ''}
            >
              Wykres VIII
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(9)}
              className={graph === 9 ? 'activeclass' : ''}
            >
              Wykres IX
            </Button>
          </li>
        </UL>
      </NavigationBar>

      {graph === 1 && (
        <Graph
          data={graphData1}
          Title={'Wykres I'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 2 && (
        <Graph
          data={graphData2}
          Title={'Wykres II'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 3 && (
        <Graph
          data={graphData3}
          Title={'Wykres III'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 4 && (
        <Graph
          data={graphData4}
          Title={'Wykres IV'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 5 && (
        <Graph
          data={graphData5}
          Title={'Wykres V'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 6 && (
        <Graph
          data={graphData6}
          Title={'Wykres VI'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 7 && (
        <Graph
          data={graphData7}
          Title={'Wykres VII'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 8 && (
        <Graph
          data={graphData8}
          Title={'Wykres VIII'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 9 && (
        <Graph
          data={graphData9}
          Title={'Wykres IX'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
    </>
  )
}

export default ZAD1
