import { useState } from 'react'
import { Button, NavigationBar, UL } from '../containers'
import Graph from '../graph/Graph'
import { za, zp, zf, N, Tc, widmo } from '../functions'
//@ts-ignore
import linspace from 'linspace'
import * as math from 'mathjs'

const ZAD2 = () => {
  const n: Array<number> = linspace(0, Tc, N)
  const graphData1: Array<object> = []
  const graphData2: Array<object> = []
  const graphData3: Array<object> = []
  const graphData4: Array<object> = []
  const graphData5: Array<object> = []
  const graphData6: Array<object> = []
  const graphData7: Array<object> = []
  const graphData8: Array<object> = []
  const graphData9: Array<object> = []
  var temp: Array<number> = []
  n.map(element => temp.push(za(element, 0.5)))
  const widmo1 = widmo(temp)
  for (let i = 0; i < temp.length; i++) {
    graphData1.push({ x: widmo1.fk[i], y: widmo1.M[i] })
  }
  temp = []
  n.map(element => temp.push(za(element, 7)))
  const widmo2 = widmo(temp)
  for (let i = 0; i < temp.length; i++) {
    graphData2.push({ x: widmo2.fk[i], y: widmo2.M[i] })
  }
  temp = []
  n.map(element => temp.push(za(element, 50)))
  const widmo3 = widmo(temp)
  for (let i = 0; i < temp.length; i++) {
    graphData3.push({ x: widmo3.fk[i], y: widmo3.M[i] })
  }
  temp = []
  n.map(element => temp.push(zp(element, 0.3)))
  const widmo4 = widmo(temp)
  for (let i = 0; i < temp.length; i++) {
    graphData4.push({ x: widmo4.fk[i], y: widmo4.M[i] })
  }
  temp = []
  n.map(element => temp.push(zp(element, 2.57)))
  const widmo5 = widmo(temp)
  for (let i = 0; i < temp.length; i++) {
    graphData5.push({ x: widmo5.fk[i], y: widmo5.M[i] })
  }
  temp = []
  n.map(element => temp.push(zp(element, 3 * math.pi)))
  const widmo6 = widmo(temp)
  for (let i = 0; i < temp.length; i++) {
    graphData6.push({ x: widmo6.fk[i], y: widmo6.M[i] })
  }
  temp = []
  n.map(element => temp.push(zf(element, 0.5)))
  const widmo7 = widmo(temp)
  for (let i = 0; i < temp.length; i++) {
    graphData7.push({ x: widmo7.fk[i], y: widmo7.M[i] })
  }
  temp = []
  n.map(element => temp.push(zf(element, math.pi / 2)))
  const widmo8 = widmo(temp)
  for (let i = 0; i < temp.length; i++) {
    graphData8.push({ x: widmo8.fk[i], y: widmo8.M[i] })
  }
  temp = []
  n.map(element => temp.push(zf(element, 4 * math.pi)))
  const widmo9 = widmo(temp)
  for (let i = 0; i < temp.length; i++) {
    graphData9.push({ x: widmo9.fk[i], y: widmo9.M[i] })
  }

  const [graph, setGraph] = useState<number | null>(1)
  return (
    <>
      <NavigationBar>
        <UL>
          <p
            style={{
              fontSize: '1rem',
              fontStyle: 'italic',
              marginTop: 0,
            }}
          >
            Modyfikacja amplitudy
          </p>
          <li>
            <Button
              onClick={() => setGraph(1)}
              className={graph === 1 ? 'activeclass' : ''}
            >
              Wykres I
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(2)}
              className={graph === 2 ? 'activeclass' : ''}
            >
              Wykres II
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(3)}
              className={graph === 3 ? 'activeclass' : ''}
            >
              Wykres III
            </Button>
          </li>
          <p
            style={{
              fontSize: '1rem',
              fontStyle: 'italic',
              marginTop: 0,
            }}
          >
            Modyfikacja fazy
          </p>
          <li>
            <Button
              onClick={() => setGraph(4)}
              className={graph === 4 ? 'activeclass' : ''}
            >
              Wykres IV
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(5)}
              className={graph === 5 ? 'activeclass' : ''}
            >
              Wykres V
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(6)}
              className={graph === 6 ? 'activeclass' : ''}
            >
              Wykres VI
            </Button>
          </li>
          <p
            style={{
              fontSize: '1rem',
              fontStyle: 'italic',
              marginTop: 0,
            }}
          >
            Modyfikacja częstotliwości
          </p>
          <li>
            <Button
              onClick={() => setGraph(7)}
              className={graph === 7 ? 'activeclass' : ''}
            >
              Wykres VII
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(8)}
              className={graph === 8 ? 'activeclass' : ''}
            >
              Wykres VIII
            </Button>
          </li>
          <li>
            <Button
              onClick={() => setGraph(9)}
              className={graph === 9 ? 'activeclass' : ''}
            >
              Wykres IX
            </Button>
          </li>
        </UL>
      </NavigationBar>

      {graph === 1 && (
        <Graph
          data={graphData1}
          Title={'Wykres I'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 2 && (
        <Graph
          data={graphData2}
          Title={'Wykres II'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 3 && (
        <Graph
          data={graphData3}
          Title={'Wykres III'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 4 && (
        <Graph
          data={graphData4}
          Title={'Wykres IV'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 5 && (
        <Graph
          data={graphData5}
          Title={'Wykres V'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 6 && (
        <Graph
          data={graphData6}
          Title={'Wykres VI'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 7 && (
        <Graph
          data={graphData7}
          Title={'Wykres VII'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 8 && (
        <Graph
          data={graphData8}
          Title={'Wykres VIII'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 9 && (
        <Graph
          data={graphData9}
          Title={'Wykres IX'}
          xTitle={'Czas'}
          yTitle={'Wartość funkcji'}
        />
      )}
    </>
  )
}

export default ZAD2
