import * as math from 'mathjs'
const fft = require('fft-js').fft
const fm: number = 5
const fn: number = 50
export const Tc: number = 1
const fs: number = 2*fn
export const N: number = Tc * fs




export const FFT = (vect:Array<number>)=> {
    const n = vect.length
    if( n && (n & (n - 1)) === 0)
      return fft(vect)
  
    const size = math.ceil(math.log2(n))
    const missingSize = Number(math.pow(2,size))-n
    
    for (let i = 0; i < missingSize;  i++) {
         vect.push(0)
    }

   return fft(vect)
 }





 const m = (t: number) => {
  return math.sin(2 * math.pi * fm * t)
}
export const za = (t: number,ka:number) => {
  return (ka * m(t) + 1) * math.cos(2 * math.pi * fn * t)
}

export const zp = (t: number,kp:number) => {
  return math.cos(2 * math.pi * fn * t + kp * m(t))
}

export const zf = (t: number,kf:number) => {
  return math.cos(2.*math.pi*fn*t+(kf/fm)*m(t))
}


//wiem co jest w kodzie to leci any :)
export const trim = (vect:Array<number>,limit:number)=>{
  const max = Math.max(...vect)
  const low = max - limit

 return vect.map((element:any)=>{
    return (element >=low && element <= max) ? element : NaN
 
  })
 
}

export const trimToWidth = (vect:Array<number>)=>{
  const width = vect.filter(element => !Number.isNaN(element))
  return (width[width.length-1] - width[0]) 
}

export const widmo = (vect:Array<number>)=>{
var  fftTable = FFT(vect)
const Re:Array<number>=[]
const Im:Array<number>=[]
for (let i = 0; i < fftTable.length; i++) {
  Re.push(fftTable[i][0])
  Im.push(fftTable[i][1])
}

let M=[]
for (let k = 0; k < Re.length / 2 - 1; k++) {
  M.push(math.sqrt(Re[k] * Re[k] + Im[k] * Im[k]))
}
let fk =[]
for (let k = 0; k < M.length; k++) {
  M[k] = 10 * math.log10(M[k])
  fk.push((k * fs) / N)
}
 return {fk,M}
}

