import {
  XYPlot,
  VerticalGridLines,
  HorizontalGridLines,
  XAxis,
  YAxis,
  LineSeries,
  //@ts-ignore
} from 'react-vis'

import '../../../node_modules/react-vis/dist/style.css'
//@ts-ignore
import html2canvas from 'html2canvas'
import { Wrapper, GraphDiv, Button } from '../containers'
type GraphProps = {
  data: Array<object>
  Title: string
  xDomain?: {}
  yDomain?: {}
  xTitle: string
  yTitle: string
}

const Graph = ({
  data,
  Title,
  xDomain,
  yDomain,
  xTitle,
  yTitle,
}: GraphProps) => {
  const id = Math.floor(Math.random() * 100) + 1
  const windowSize = {
    height: `${(window.innerHeight - 100).toString()}px`,
    width: `${(window.innerWidth - 20).toString()}px`,
  }

  const graphSize = {
    height: window.innerHeight - 150,
    width: window.innerWidth - 257,
  }

  return (
    <>
      <Wrapper windowSize={windowSize}>
        <GraphDiv>
          <div id={id.toString()}>
            <div
              style={{
                fontFamily: 'Arial',
                fontSize: '1.8rem',
                textAlign: 'center',
                margin: '1rem',
              }}
            >
              <span>{Title}</span>
            </div>
            <XYPlot
              height={graphSize.height}
              width={graphSize.width}
              xDomain={xDomain}
              yDomain={yDomain}
            >
              <VerticalGridLines />
              <HorizontalGridLines />
              <XAxis title={xTitle} position="middle" />
              <YAxis title={yTitle} position="middle" />
              <LineSeries data={data} />
            </XYPlot>
          </div>
        </GraphDiv>
      </Wrapper>
    </>
  )
}

export default Graph
